'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import { loginUser } from '@/services/api'
import { useAuth } from '@/context/AuthContext'
import { Box, Button, TextField, Typography } from '@mui/material'

export default function LoginPage() {
  const { login } = useAuth()
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [error, setError] = useState('')
  const router = useRouter()

  const handleLogin = () => {
    const result = loginUser(email, password)
    if (!result.success) {
      setError(result.message)
    } else {
      login(email)
      router.push('/dashboard')
    }
  }

  return (
    <Box display="flex" flexDirection="column" maxWidth={400} margin="auto" gap={3} mt={10}>
      <Typography variant="h5">Login</Typography>

      <TextField label="E-Mail" value={email} onChange={(e) => setEmail(e.target.value)} />
      <TextField label="Passwort" type="password" value={password} onChange={(e) => setPassword(e.target.value)} />

      {error && <Typography color="error">{error}</Typography>}

      <Button variant="contained" onClick={handleLogin}>
        Einloggen
      </Button>

      {/* ➡️ Neuer Button */}
      <Button variant="outlined" onClick={() => router.push('/register')}>
        Noch kein Account? Registrieren
      </Button>
    </Box>
  )
}
